body {
  margin: 0;
  padding: 0;
  background-color: #f5f5f5;
  color: #000;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}
html {
  box-sizing: border-box;
}
* {
  box-sizing: inherit;
}
*:focus {
  outline: 2px solid #9D5CD0;
}
a {
  color: #000;
  text-decoration: none;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
button {
  border: none;
  font-family: 'Open Sans', sans-serif;
  background: none;
}
img {
  max-width: 100%;
}
input {
  border: none;
  font-family: 'Open Sans', sans-serif;
  background: none;
}
fieldset {
  margin: 0;
  padding: 0;
  border: none;
}
.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
}
.main {
  width: 1450px;
  display: flex;
  justify-content: space-between;
  margin: 100px auto;
}
.disable {
  display: none;
}
.bold {
  font-weight: 600;
}
.notice {
  margin-bottom: 35px;
  margin-top: 35px;
  padding: 18px 24px;
  text-indent: 32px;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
  border-radius: 15px;
  background-color: #EFF0F2;
  background-image: url('../img/light.png');
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: 20px 13px;
}
.code {
  background-color: #fffded9c;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/OpenSans-Regular.ttf) format('ttf'), url('../fonts/opensans-regular-webfont.woff2') format('woff2'), url('../fonts/opensans-regular-webfont.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/OpenSans-SemiBold.ttf) format('ttf'), url('../fonts/opensans-semibold-webfont.woff2') format('woff2'), url('../fonts/opensans-semibold-webfont.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Open Sans';
  src: url(../fonts/OpenSans-Bold.ttf) format('ttf'), url('../fonts/opensans-bold-webfont.woff2') format('woff2'), url('../fonts/opensans-bold-webfont.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-regular.ttf) format('ttf'), url('../fonts/montserrat-regular.woff2') format('woff2'), url('../fonts/montserrat-regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-medium.ttf) format('ttf'), url('../fonts/montserrat-medium.woff2') format('woff2'), url('../fonts/montserrat-medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-semibold.ttf) format('ttf'), url('../fonts/montserrat-semibold.woff2') format('woff2'), url('../fonts/montserrat-semibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes bg-image-first {
  0% {
    background-image: url('../img/birmingham-museums-trust-MK_98NuC4-4-unsplash2.jpg');
  }
  33.33% {
    background-image: url('../img/birmingham-museums-trust-MK_98NuC4-4-unsplash 1.jpg');
  }
  66.66% {
    background-image: url('../img/birmingham-museums-trust-MK_98NuC4-4-unsplash 3.jpg');
  }
  100% {
    background-image: url('../img/birmingham-museums-trust-MK_98NuC4-4-unsplash2.jpg');
  }
}
.course {
  width: 950px;
  padding: 60px 10px 0px;
}
.course__search {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 52px;
  margin-bottom: 50px;
}
.course__search-input {
  width: 100%;
  height: 100%;
  padding: 16px 110px 16px 67px;
  background-color: #EFF0F2;
  border-radius: 15px;
  background-image: url('../img/search.png');
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: 20px 13px;
}
.course__search-btn {
  position: absolute;
  top: 0;
  right: 0;
  width: 110px;
  height: 100%;
  border: 1px solid #909090;
  border-radius: 15px;
  font-size: 16px;
  line-height: 16px;
  color: #000;
  font-weight: 400;
  font-weight: 500;
}
.course__theme {
  margin-bottom: 30px;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
}
.course__title {
  margin: 0;
  padding: 0;
  margin-bottom: 8px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
  font-weight: 400;
}
.course__title-h3 {
  font-size: 20px;
  line-height: 24px;
  color: #000;
  font-weight: 400;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
}
.course__btn {
  width: 250px;
  height: 52px;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
  font-weight: 500;
  color: #fff;
  background-color: #61A5C2;
  border-radius: 20px;
}
.course__item {
  position: relative;
  padding-left: 23px;
}
.course__item:before {
  content: '';
  position: absolute;
  top: 10px;
  left: 10px;
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #000;
}
.course__title-h4 {
  margin: 0;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  font-weight: 400;
}
.course__list-0 {
  margin-bottom: 0;
}
.course__table {
  margin-bottom: 35px;
  border-color: #909090;
}
.course__table caption {
  margin-bottom: 5px;
  text-align: left;
}
.course__table th {
  text-align: left;
}
.course__table th,
.course__table td {
  padding: 9px 11px;
}
.course__code {
  position: relative;
  margin-bottom: 35px;
}
.course__code-result {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.course__code-result input {
  width: 100%;
  height: 52px;
  background-color: #fff;
  border: 1px solid #909090;
  border-radius: 15px;
}
.course__code-result button {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 15px;
}
.bar {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 850px;
  padding: 55px 45px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  border-radius: 20px;
  background-color: #EFF0F2;
}
.bar__title {
  margin: 0;
  padding: 0;
  margin-bottom: 28px;
  font-size: 24px;
  line-height: 24px;
  color: #000;
  font-weight: 400;
  font-weight: 700;
}
.bar__name {
  margin: 0;
  padding: 0;
  margin-bottom: 28px;
  font-size: 20px;
  line-height: 20px;
  color: #000;
  font-weight: 400;
  font-weight: 500;
}
.bar__list-theme {
  font-size: 16px;
  line-height: 20px;
  color: #000;
  font-weight: 400;
}
.bar__item-lesson {
  position: relative;
  margin-bottom: 17px;
  padding-left: 15px;
}
.bar__item-lesson:first-child {
  font-weight: 600;
}
.bar__item-lesson:not(:first-child) {
  padding-left: 40px;
  padding-right: 50px;
  cursor: pointer;
}
.bar__item-lesson:not(:first-child)::before {
  left: 10px;
}
.bar__item-lesson::before {
  content: '1';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
}
.bar__item-lesson:nth-child(2)::before {
  content: '1.1';
}
.bar__item-lesson:nth-child(3)::before {
  content: '1.2';
}
.bar__item-lesson:nth-child(4)::before {
  content: '1.3';
}
.bar__list-lesson-2 li:nth-child(1)::before {
  content: '2';
}
.bar__list-lesson-2 li:nth-child(2)::before {
  content: '2.1';
}
.bar__list-lesson-2 li:nth-child(3)::before {
  content: '2.2';
}
.bar__list-lesson-2 li:nth-child(4)::before {
  content: '2.3';
}
.bar__btn {
  align-self: center;
  width: 265px;
  height: 52px;
  margin-bottom: 25px;
  border: 1px solid #909090;
  border-radius: 20px;
  font-size: 16px;
  line-height: 16px;
  color: #000;
  font-weight: 400;
  cursor: pointer;
}
.bar__btn:hover {
  color: purple;
  border-color: purple;
  transition: 0.6s;
}
